/* eslint-disable */
import React , { useState, useEffect}from 'react';
import {Nav, TabContent} from 'react-bootstrap';
import {useHistory} from 'react-router-dom';
import Data from './data.js';
import img13 from './airpodspro.jpg';
import img14  from './airpods.jpg';
import { connect } from 'react-redux';



function Music(props){
  let history=useHistory();
  let [tab,changetab]=useState(0);
  let [music, changemusic]=useState(Data);

  return (

    <div>
    <Nav justify variant="tabs" defaultActiveKey="link-0">
    <Nav.Item>
      <Nav.Link eventKey="link-0" onClick={()=>{
        changetab(0)
      }}>Airpods</Nav.Link>
    </Nav.Item>
    <Nav.Item>
      <Nav.Link eventKey="link-1" onClick={()=>{
        changetab(1)
      }}>종류 KINDS</Nav.Link>
    </Nav.Item>
    
    
  </Nav>

  <button  className="btn btn-danger" onClick={()=>{ 
    props.dispatch({type : '항목추가', payload : {id : music[13].id, name : music[13].title, quan : 1} }) 
    history.push('/cart')
  }}>주문하기</button>
  <button onClick={()=>{ history.goBack() }} className="btn btn-danger">뒤로가기</button> 

<TabContent tab={tab}/>
  </div>
)

function TabContent(props) {
  if(props.tab===0){
    return (
    <div>
      <img src={img13}  height="400" align="left"/>
      <br/><br/><br/><br/><br/><h2>{music[13].title}</h2><br/>{music[13].price}<br/>{music[13].display}
       </div>
    )}
  else if(props.tab===1){
    return (
      <div>
        <img src={img13} width="28%"  height="400" align="left"/>
        <img src={img14} width="28%" height="400" align="left"/>
         </div>
      )}
  
}
}



function state를props화 (state){
  console.log(state)
  return {
      state : state.reducer
  }
}
export default connect(state를props화)(Music);

