/* eslint-disable */
import React , { useState, useEffect}from 'react';
import {Nav, TabContent} from 'react-bootstrap';
import {useHistory} from 'react-router-dom';
import {connect} from 'react-redux';
import Data from './data.js';
import img15 from './watch6.jpg';
import img16 from './watchse.jpg';
import img17 from './watch3.jpg';



function Watch(props){
  let history=useHistory();
  let [tab,changetab]=useState(0);
  let [watch, changewatch]=useState(Data);

  return (

    <div>
          <Nav justify variant="tabs" defaultActiveKey="link-0">
          <Nav.Item>
            <Nav.Link eventKey="link-0" onClick={()=>{
              changetab(0)
            }}>Apple Watch</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-1" onClick={()=>{
              changetab(1)
            }}>종류 KINDS</Nav.Link>
          </Nav.Item>
          
          
        </Nav>

        <button  className="btn btn-danger" onClick={()=>{ 
          props.dispatch({type : '항목추가', payload : {id : watch[15].id, name : watch[15].title, quan : 1} }) 
          history.push('/cart')
        }}>주문하기</button>
        <button onClick={()=>{ history.goBack() }} className="btn btn-danger">뒤로가기</button> 

    <TabContent tab={tab}/>
        </div>
    )

    function TabContent(props) {
      if(props.tab===0){
        return (
        <div>
          <img src={img15}  height="400" align="left"/>
          <br/><br/><br/><br/><br/><h2>{watch[15].title}</h2><br/>{watch[15].price}<br/>{watch[15].display}
           </div>
        )}
      else if(props.tab===1){
        return (
          <div>
            <img src={img15} width="28%" height="400" align="left"/>
            <img src={img16} width="28%" height="400" align="left"/>
             </div>
          )}
      
    }
}

//export default Watch
function state를props화(state){
  return {
    state : state
  }
}

export default connect(state를props화)(Watch);