/* eslint-disable */
import React , { useState, useEffect}from 'react';
import {Nav, TabContent} from 'react-bootstrap';
import {useHistory} from 'react-router-dom';
import {connect} from 'react-redux';
import Data from './data.js';
import img1 from './pro.jpg';
import img2 from './12.jpg';
import img3 from './se.jpg';
import img4 from './11.jpg';



function Iphone(props){
  let history=useHistory();
  let [tab,changetab]=useState(0);
  let [iphone, changeiphone]=useState(Data);

  return (

    <div>
          <Nav justify variant="tabs" defaultActiveKey="link-0">
          <Nav.Item>
            <Nav.Link eventKey="link-0" onClick={()=>{
              changetab(0)
            }}>iPhone </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-1" onClick={()=>{
              changetab(1)
            }}>종류 KINDS</Nav.Link>
          </Nav.Item>
          
          
        </Nav>

    
    <button  className="btn btn-danger" onClick={()=>{ 
      props.dispatch({type : '항목추가', payload : {id : iphone[0].id, name : iphone[0].title, quan : 1} }) 
      history.push('/cart')
    }}>주문하기</button>
    <button onClick={()=>{ history.goBack() }} className="btn btn-danger">뒤로가기</button> 
    <TabContent tab={tab} />

    </div>
    )

    

    function TabContent(props) {
      if(props.tab===0){
        return (
        <div>
          <img src={img1}  height="600" align="left"/>
          <br/><br/><br/><br/><br/><h2>{iphone[1].title}</h2><br/>{iphone[1].price}<br/>{iphone[1].display}<br/>{iphone[1].cellular}
          </div>
          
        )}
      else {
        return (
          <div>
            <img src={img1}  width="25%" height="600" align="left"/>
            <img src={img2}  width="25%" height="600" align="center"/>
            <img src={img3}  width="25%" height="600" align="right"/>
            <img src={img4}  width="25%" height="600" align="right"/>
            </div>
          )}
    }
}

function state를props화(state){
  return {
    state : state
  }
}

export default connect(state를props화)(Iphone);



