/* eslint-disable */
import React , { useState, useEffect}from 'react';
import {Nav, TabContent} from 'react-bootstrap';
import {useHistory} from 'react-router-dom';
import {connect} from 'react-redux';
import Data from './data.js';
import img1 from './pro.jpg';
import img2 from './12.jpg';
import img3 from './se.jpg';
import img4 from './11.jpg';



function Test(props){
  let history=useHistory();
  let [tab,changetab]=useState(0);
  let [iphone, changeiphone]=useState(Data);

  return (

    <div>
          <Nav justify variant="tabs" defaultActiveKey="link-0">
          <Nav.Item>
            <Nav.Link eventKey="link-0" onClick={()=>{
              changetab(0)
            }}>iPhone 12 Pro</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-1" onClick={()=>{
              changetab(1)
            }}>iPhone 12</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-2" onClick={()=>{
              changetab(2)
            }}>iPhone 12 SE</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-3" onClick={()=>{
              changetab(3)
            }}>iPhone 11</Nav.Link>
          </Nav.Item>
          
        </Nav>

    
    <button onClick={()=>{ 
  
      props.dispatch({type : '항목추가', payload : {id : iphone.id, name : iphone.title, quan : 1} }) 
      history.push('/cart')
      

    }}>주문하기</button>
    
    <TabContent tab={tab} />

        </div>
    )

    function Info(props){
      return (
        <p>재고: {props.재고[0]}</p>
      )
    }

    function TabContent(props) {
      if(props.tab===0){
        return (
        <div>
          <img src={img1}  height="600" align="left"/>
          <br/><br/><br/><br/><br/><h2>{iphone[0].title}</h2><br/>{iphone[0].price}<br/>{iphone[0].display}<br/>{iphone[0].cellular}
          <button onClick={()=>{
                                    
          }}>주문하기</button> 
          <button onClick={()=>{ history.goBack() }} className="btn btn-danger">뒤로가기</button> </div>
          
        )}
      else if(props.tab===1){
        return (
          <div>
            <img src={img2} height="600" align="left"/>
            <br/><br/><br/><br/><br/><h2>{iphone[1].title}</h2><br/>{iphone[1].price}<br/>{iphone[1].display}<br/>{iphone[1].cellular}
            <button className="btn btn-danger">주문하기</button> 
          <button onClick={()=>{ history.goBack() }} className="btn btn-danger">뒤로가기</button> </div>
          )}
      else if(props.tab===2){
        return (
          <div>=-                                                                                                                                                                                                                    
            <img src={img3}  height="600" align="left"/>
            <br/><br/><br/><br/><br/><h2>{iphone[2].title}</h2><br/>{iphone[2].price}<br/>{iphone[2].display}<br/>{iphone[2].cellular}
            <button className="btn btn-danger">주문하기</button> 
          <button onClick={()=>{ history.goBack() }} className="btn btn-danger">뒤로가기</button> </div>
          )}
      else {
        return (
          <div>
            <img src={img4}  height="600" align="left"/>
            <br/><br/><br/><br/><br/><h2>{iphone[3].title}</h2><br/>{iphone[3].price}<br/>{iphone[3].display}<br/>{iphone[3].cellular}
            <button className="btn btn-danger">주문하기</button> 
          <button onClick={()=>{ history.goBack() }} className="btn btn-danger">뒤로가기</button> </div>
          )}
    }
}

function state를props화(state){
  return {
    state : state
  }
}

export default connect(state를props화)(Test);



