/* eslint-disable */
import React , { useState, useEffect}from 'react';
import {Nav, TabContent} from 'react-bootstrap';
import {useHistory} from 'react-router-dom';
import {connect} from 'react-redux';
import Data from './data.js';
import img9 from './macbookair.png';
import img10 from './macbookpro.jpg';
import img11 from './imac13.jpg';
import img12 from './imac27.jpg';



function Mac(props){
  let history=useHistory();
  let [tab,changetab]=useState(0);
  let [mac, changemac]=useState(Data);

  return (

    <div>
          <Nav justify variant="tabs" defaultActiveKey="link-0">
          <Nav.Item>
            <Nav.Link eventKey="link-0" onClick={()=>{
              changetab(0)
            }}>Mac</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-1" onClick={()=>{
              changetab(1)
            }}>종류 KINDS</Nav.Link>
          </Nav.Item>
          
        </Nav>

        <button  className="btn btn-danger" onClick={()=>{ 
          props.dispatch({type : '항목추가', payload : {id : mac[9].id, name : mac[9].title, quan : 1} }) 
          history.push('/cart')
        }}>주문하기</button>
        <button onClick={()=>{ history.goBack() }} className="btn btn-danger">뒤로가기</button> 
        <TabContent tab={tab} />
        </div>
    )

    function TabContent(props) {
      if(props.tab===0){
        return (
        <div>
          <img src={img9}  height="400" align="left"/>
          <br/><br/><br/><br/><br/><h2>{mac[9].title}</h2><br/>{mac[9].price}<br/>{mac[9].display}
           </div>
        )}
      else if(props.tab===1){
        return (
          <div>
            <img src={img9} width="25%" height="400" align="left"/>
            <img src={img10} width="25%" height="400" align="right"/>
            <img src={img11} width="25%" height="400" align="center"/>
            <img src={img12} width="25%"  height="400" align="right"/>
             </div>
          )}
     
      
    }
}

//export default Mac
function state를props화(state){
  return {
    state : state
  }
}

export default connect(state를props화)(Mac);