/* eslint-disable */
import React , { useState, useEffect}from 'react';
import {Nav, TabContent} from 'react-bootstrap';
import {useHistory} from 'react-router-dom';
import {connect} from 'react-redux';
import Data from './data.js';
import img5 from './ipadpro.jpeg';
import img6 from './ipadair.jpeg';
import img7 from './ipad.jpeg';
import img8 from './ipadmini.jpeg';



function Ipad(props){
  let history=useHistory();
  let [tabipad,changetabipad]=useState(5);
  let [ipad, changeipad]=useState(Data);

  return (

    <div>
          <Nav justify variant="tabipad" defaultActiveKey="link-5">
          <Nav.Item>
            <Nav.Link eventKey="link-5" onClick={()=>{
              changetabipad(5)
            }}>iPad</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-6" onClick={()=>{
              changetabipad(6)
            }}>종류 KINDS</Nav.Link>
          </Nav.Item>
          
        </Nav>

        <button  className="btn btn-danger" onClick={()=>{ 
          props.dispatch({type : '항목추가', payload : {id : ipad[5].id, name : ipad[5].title, quan : 1} }) 
          history.push('/cart')
        }}>주문하기</button>
        <button onClick={()=>{ history.goBack() }} className="btn btn-danger">뒤로가기</button> 
    <TabContentipad tabipad={tabipad}/>
        </div>
    )

    function TabContentipad(props) {
      if(props.tabipad===5){
        return (
        <div>
          <img src={img5}   align="left"/>
          <br/><br/><br/><br/><br/><h2>{ipad[5].title}</h2><br/>{ipad[5].price}<br/>{ipad[5].display}<br/>{ipad[5].cellular}
           </div>
        )}
      else if(props.tabipad===6){
        return (
          <div>
            <img src={img5} width="25%"  height="400" align="left"/>
            <img src={img6} width="25%" height="400" align="center"/>
            <img src={img7} width="25%" height="400" align="right"/>
            <img src={img8} width="25%" height="400" align="right"/>
             </div>
          )}
    }
}

//export default Ipad
function state를props화(state){
  return {
    state : state
  }
}

export default connect(state를props화)(Ipad);